.app {
    overflow-x: hidden;
    position: absolute;
    background-color: #FFF;
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    overflow: hidden;
}

.img {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.coating {
    width: inherit;
}

/*
Header things
 */

.container_up_space {
    height: 120px;
}

.container_down_space {
    height: 56px;
}

.header_up_space {
    height: 20px;
}

.menu_item__container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.menu_item {
    margin-left: 20px;
    margin-right: 20px;
    font-size: 24px;
    font-family: Muller, sans-serif;
    font-weight: 600;
    font-display: swap;
}

.menu_item__language {
    color: #4C37C2;
}

.link__active {
    color: #4C37C2;
}

.menu_item__ordinary {
    color: #232323;
}

.menu_item__main {
    visibility: hidden;
    width: 0;
    height: 0;
}

.menu_item:hover {
    cursor: pointer;
}

.header_logo {
    font-family: Roboto, sans-serif;
    font-weight: 500;
    font-display: swap;
    font-size: 48px;
    margin-left: 64px;
}

.header_logo__image {
    width: 100px;
}

.header_logo__mobile {
    display: none;
}

.header_logo__mobile__active {
    visibility: initial;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.header_logo:hover {
    cursor: pointer;
}

.header_menu {
    width: 50%;
    position: relative;
    top: 6px;
    margin-left: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-right: 50px;
}

.header_languages__fullsize {
    /*width: 40%;
      max-width: 400px;
    */
    z-index: 20;
    position: relative;
    top: 6px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-right: 50px;
    align-items: center;
}

.header_languages__shortsize {
    /*width: 40%;
      max-width: 400px;
    */
    z-index: 20;
    position: relative;
    top: 6px;
    margin-left: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-right: 50px;

    width: 0px;
    visibility: hidden;
}

.header__language_button {
    color: #232323;
    font-size: 22px;
    font-family: Monserrat, sans-serif;
    font-weight: 600;
    font-display: swap;
    margin-right: 20px;
    margin-left: 20px;
}

.header__language_button:hover {
    cursor: pointer;
}

.header__language_button__inactive {
    opacity: 0.2;
}

.header__language_button__active {
    opacity: 1;
}

.header {
    align-items: center;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.header__short_form {
    position: relative;
    top: 20px;
    margin-right: 64px;
    border: 1px solid rgba(182, 182, 182, .4);
    border-radius: 20px;
    width: 350px;
}

.header__short_form__content {
    margin: 20px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
}

.header__button {
    font-family: Monserrat, sans-serif;
    font-size: 22px;
    font-weight: 700;
    font-display: swap;
}

.header__button:hover {
    cursor: pointer;
}

.header__input {
    background-color: transparent;
    border: 0px solid;
    color: #232323;
    width: 200px;
    font-family: Monserrat, sans-serif;
    font-size: 22px;
    font-weight: normal;
    font-display: swap;
}

textarea:focus, input:focus {
    outline: none;
}

.header__short_form__toast {
    font-family: Monserrat, sans-serif;
    font-size: 18px;
    font-weight: normal;
    font-display: swap;
    margin: 20px;
}

.header__short_form__toast__error {
    color: #D32161;
}

.header__short_form__toast__success {
    color: #21D328;
}

