/*
Instructions
 */

.stepper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.steps__array {
    margin-top: 64px;
}

.step__button {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.step__button_text {
    position: absolute;
    z-index: 5;
    color: #FFF;
    font-family: Monserrat, sans-serif;
    font-weight: 500;
    font-size: 26px;
}

.stepper__tick {
    width: 4px;
    height: 100%;
    background: linear-gradient(#3617E5, #8572F0);
}

.stepper__tick_disabled {
    background: initial;
}

.step {
    display: flex;
    flex-direction: row;
}

.step_text {
    margin-left: 32px;
}

.step_text__item {
    font-family: Monserrat, sans-serif;
    font-weight: 500;
    font-display: swap;
    font-size: 32px;
    line-height: 1.4em;
    margin-bottom: 30px;
}

@media (max-width: 1650px) {
    .header__short_form {
        width: 250px;
    }
}

@media (min-width: 1680px) {
    .app {
        max-width: calc(1680px - 96px * 2);
        margin-left: calc(50vw - (1680px - 96px * 2) / 2);
        margin-right: calc(50vw - (1680px - 96px * 2) / 2);
    }
}

@media (max-width: 1450px) {
    .header {
        flex-wrap: wrap;
    }

    .header__short_form {
        width: 0px;
        visibility: hidden;
        height: 0px;
    }

    .header_menu {
        width: 60%;
    }
}

@media (max-width: 1300px) {
    .footer {
        flex-direction: column;
    }

    .footer_column {
        margin-left: 64px;
        margin-top: 15px;
    }

    footer_column__ending {
        margin-right: 0px;
    }

    .footer_logo {
        top: initial;
    }

    .rate_form {
        width: 50%;
    }

    .footer__short_form {
